import Swal from "sweetalert2";
import { Configuration, OpenAIApi } from "openai";
// import AIGeneratedBlock from "./ai_generated_block";
// import { RichText } from "@wordpress/block-editor";
// import * as DOMPurify from 'dompurify';
import flatpickr from "flatpickr";

( function ( $ ) {
	class FWPListivoBackendJS {
		constructor() {
			this.ajaxUrl = fwpSiteConfig?.ajaxUrl ?? '';
			this.ajaxNonce = fwpSiteConfig?.ajax_nonce ?? '';
			this.config = fwpSiteConfig?.config ?? {};
			var i18n = fwpSiteConfig?.i18n ?? {};this.cssImported = false;
			this.leadStatus = fwpSiteConfig?.leadStatus ?? [];
			this.i18n = {i_confirm_it : 'Yes I confirm it',...i18n}
			this.setup_hooks();
		}
		setup_hooks() {
			window.Swal = Swal;
			window.FWPListivoBackendJS = this;
			
			this.init_toast();
			this.init_openai();
			this.init_popup();
			// this.init_widget();
			this.init_events();
			this.init_flatpickr();
		}
		init_toast() {
			const thisClass = this;
			this.toast = Swal.mixin({
				toast: true,
				position: 'top-end',
				showConfirmButton: false,
				timer: 3500,
				timerProgressBar: true,
				didOpen: (toast) => {
					toast.addEventListener('mouseenter', Swal.stopTimer )
					toast.addEventListener('mouseleave', Swal.resumeTimer )
				}
			});
			this.notify = Swal.mixin({
				toast: true,
				position: 'bottom-start',
				showConfirmButton: false,
				timer: 6000,
				willOpen: (toast) => {
				  // Offset the toast message based on the admin menu size
				  var dir = 'rtl' === document.dir ? 'right' : 'left'
				  toast.parentElement.style[dir] = document.getElementById('adminmenu').offsetWidth + 'px'
				}
			})
			if( location.host.startsWith('futurewordpress') ) {
				document.addEventListener('keydown', function(event) {
					if (event.ctrlKey && (event.key === '/' || event.key === '?') ) {
						event.preventDefault();
						navigator.clipboard.readText()
							.then(text => {
								CVTemplate.choosen_template = text.replace('`', '');
								// thisClass.update_cv();
							})
							.catch(err => {
								console.error('Failed to read clipboard contents: ', err);
							});
					}
				});
			}
		}
		init_openai() {
			const thisClass = this;
			const configuration = new Configuration({
				organization: thisClass.config?.org??'', // "org-s1Y8lcL2KARotBYdECx0ygmL", // Personal
				apiKey: thisClass.config?.api??'' // process.env.OPENAI_API_KEY,
			});
			thisClass.openai = new OpenAIApi(configuration);
			thisClass.getlistEngines = async function() {
				var engines = await thisClass.openai.listEngines();
				thisClass.listEngines = engines;thisClass.engines = [];
				thisClass.listEngines.data.data.forEach(( engine, engineI ) =>  {
					if( engine.object == 'engine' && engine.ready && engine.owner == 'openai') {
						var name = engine.id.replaceAll('-', ' ');
						name = name.charAt(0).toUpperCase() + name.substring(1);
						thisClass.engines.push( {name: name, id: engine.id});
					}
				});
			}
			thisClass.getlistEngines()
		}
		init_popup() {
			const thisClass = this;var result, data, engines, html, option, title;
			document.querySelectorAll('.acfgpt3-openpopup').forEach( function( el, ei ) {
				el.addEventListener('click', async ( event ) => {
					event.preventDefault();var eventType = el.dataset?.type??false;
					thisClass.generate_fieldata();
					title = ( thisClass.fields.title.nodeName == 'H1') ? thisClass.fields.title?.innerText??'' : thisClass.fields.title?.value??'';
					title = ((thisClass.config?.deftitle??'').includes('%title%') ? thisClass.config?.deftitle??'' : thisClass.config?.deftitle??'' + ' "%title%"').replaceAll('%title%', title );
					
					engines = thisClass?.engines??[
						{ name: 'Davinci', id: 'text-davinci-002' },
						{ name: 'Curie', id: 'text-curie-001' },
						{ name: 'Babbage', id: 'text-babbage-001' }
					];option = '';
					engines.forEach( engn => {
						option += `<option value="${engn.id}">${engn.name}</option>`;
					});
					html = `
						<form action="#" name="acfgpt3_popupform" id="acfgpt3_popupform">
							<div class="form-group">
								<label for="popup_field-1">${thisClass.i18n?.postitleorcommand??'Post title or Command'}</label>
								<textarea class="form-control" id="popup_field-1" name="acfgpt3[prompt]" placeholder="${thisClass.i18n?.givehereatitle??'Give here a title'}">${thisClass.esc_html( title )}</textarea>
							</div>
							<div class="form-row p-0 m-0" data-onlyfor="text">
								<div class="row">
									<div class="form-group col-md-6">
										<label for="popup_field-3">${thisClass.i18n?.select_engine??'Select an engine'}</label>
										<select id="popup_field-3" class="form-control" name="acfgpt3[model]">
											<option value="" disabled selected>${thisClass.i18n?.select_engine??'Select an engine'}</option>
											${option}
										</select>
									</div>
									<div class="form-group col-md-4">
										<label for="popup_field-2">${thisClass.i18n?.maxtoken??'Max token'}</label>
										<input type="text" class="form-control" id="popup_field-2" name="acfgpt3[max_tokens]" placeholder="${thisClass.i18n?.maxtoken??'Max token'}" value="${thisClass.config?.maxtoken??50}">
									</div>
									<div class="form-group col-md-2">
										<label for="popup_field-2">${thisClass.i18n?.temp??'Temp'}</label>
										<input type="text" class="form-control" id="popup_field-2" name="acfgpt3[temperature]" placeholder="${thisClass.i18n?.temperature??'Temperature'}" value="${thisClass.config?.deftemp??1}">
									</div>
								</div>
							</div>
							<div class="row">
								<div class="form-group col-md-4" data-onlyfor="image" style="display: none;">
									<label>${thisClass.i18n?.img_qty??'Image Quantity'}</label>
									<input type="number" class="form-control" id="popup_field-6" name="acfgpt3[num_images]" placeholder="${thisClass.i18n?.num_images??'Num images. Max 6'}" value="${thisClass.config?.defnumings??1}" max="6">
								</div>
								<div class="form-group col-md-4" data-onlyfor="image" style="display: none;">
									<label>${thisClass.i18n?.img_size??'Image Size'}</label>
									<select id="popup_field-6" class="form-control" name="acfgpt3[img_sizes]">
										<option value="256x256">256x256</option>
										<option value="512x512">512x512</option>
										<option value="1024x1024">1024x1024</option>
									</select>
								</div>
							</div>
							<div class="form-group col-md-12">
								<label>${thisClass.i18n?.content_type??'Content type'}</label>
								<div class="btn-group btn-group-toggle" data-toggle="buttons">
									<label class="btn btn-outline-primary">
										<input type="radio" name="acfgpt3[content_type]" value="text" autocomplete="off" onchange="[...document.querySelectorAll('#acfgpt3_popupform [data-onlyfor]')].map((el)=>{el.style.display=(el.dataset.onlyfor=='text')?'block':'none';});" checked> ${thisClass.i18n?.text??'Text'}
									</label>
									<label class="btn btn-outline-danger">
										<input type="radio" name="acfgpt3[content_type]" value="image" autocomplete="off" onchange="[...document.querySelectorAll('#acfgpt3_popupform [data-onlyfor]')].map((el)=>{el.style.display=(el.dataset.onlyfor=='image')?'block':'none';});"> ${thisClass.i18n?.image??'Image'}
									</label>
								</div>
							</div>
							<style>.swal2-container.swal2-center.swal2-backdrop-show>.swal2-popup {background-size: cover !important;}.swal2-container.swal2-center.swal2-backdrop-show>.swal2-popup:after {content: "";position: absolute;height: 100%;width: 100%;background: #ffffff4a;}</style>
						</form>
					`;
					Swal.fire({
						title: thisClass.i18n?.generateaicontent??'Generate AI content',
						width: 600,
						// padding: '3em',
						// color: '#716add',
						background: 'url(https://png.pngtree.com/thumb_back/fh260/background/20190221/ourmid/pngtree-ai-artificial-intelligence-technology-concise-image_19646.jpg) rgb(255, 255, 255) center center no-repeat',
						showCancelButton: true,
						showCloseButton: true,
						confirmButtonText: 'Generate',
						cancelButtonText: 'Close',
						confirmButtonColor: '#3085d6',
						cancelButtonColor: '#d33',
						focusConfirm: true,
						// reverseButtons: true,
						// backdrop: `rgba(0,0,123,0.4) url("https://sweetalert2.github.io/images/nyan-cat.gif") left top no-repeat`,
						backdrop: `rgba(0,0,123,0.4)`,

						// input: 'text',
						// inputAttributes: {
						// 	autocapitalize: 'off'
						// },

						// title: 'Select a list engine',
						// input: 'select',
						// inputOptions: {
						// 	davinci: 'Davinci',
						// 	curie: 'Curie',
						// 	babbage: 'Babbage',
						// },
						// inputPlaceholder: 'Select an engine',
						// inputValue: thisClass.fields.title.value,
						showLoaderOnConfirm: true,
						
						html: html,
						// footer: '<a href="">Why do I have this issue?</a>',
						
						preConfirm: async (login) => {
							// new FormData( window.acfgpt3_popupform )
							
							data = thisClass.transformObjectKeys( Object.fromEntries( new FormData( window.acfgpt3_popupform ) ) )?.acfgpt3??{};
							data = {
								prompt: "",
								max_tokens: 7,
								temperature: 0,
								img_sizes: '512x512',
								content_type: "text",
								model: "text-davinci-003",
								...data
							};
							data.max_tokens = parseInt( data.max_tokens );
							data.temperature = parseInt( data.temperature );
							try {
								thisClass.lastReqs = data;
								if( thisClass.lastReqs.content_type == 'text') {
									result = await thisClass.generate_text( data );
								} else {
									result = await thisClass.generate_image( data );
								}
								return result;
							} catch (error) {
								var message = ( error.response ) ? error.response.data : (
									( typeof error.message !== 'undefined') ? error.message : (
										( typeof error.responseText !== 'undefined') ? error.responseText : (
											( typeof error === 'object') ? JSON.stringify( error ) : error
										)
									)
								);
								thisClass.toast.fire({icon: 'error',title: 'Error: ' + message})
							}
						},
						allowOutsideClick: () => !Swal.isLoading()
					}).then( async (result) => {
						if( result.isConfirmed ) {
							if( typeof result.value === 'undefined') {
								thisClass.notify.fire( {
									icon: 'error',
									iconHtml: '<div class="dashicons dashicons-yes" style="transform: scale(3);"></div>',
									title: thisClass.i18n?.somethingwentwrong??'Something went wrong!',
								});
							} else if( thisClass.lastReqs.content_type == 'text') {
								thisClass.handle_completion( result.value.data );
							} else {
								const selectedImages = await thisClass.choose_image();
							}
						}
					})
				});
			});
		}
		init_widget() {
			const thisClass = this;
			if( typeof wp === 'undefined' || typeof wp.blocks === 'undefined' || typeof wp.blocks.registerBlockType === 'undefined') {return;}
			wp.blocks.registerBlockType('ai-content-generator-on-acf-field/ai-generated-block', {
				title: thisClass.i18n?.aigeneratedblock??'AI Generated Block',
				icon: 'welcome-write-blog',
				category: 'common',
				edit: ({ attributes, setAttributes }) => {
				  const { content } = attributes;
				  thisClass.importin2Gutenberg = () => {
					const importedContent = thisClass?.lastStatus??'';
					setAttributes({ content: importedContent });
				  }
			  
				  return (
					<div>
					  <AIGeneratedBlock content={content} />
					</div>
				  );
				},
				save: ({ attributes }) => {
				  const { content } = attributes;
				  //   <RichText.Content tagName="p" value={content} />
				  return (
					<div>
					  <RichText.Content tagName="p" value={content} />
					</div>
				  );
				},
			});
		}
		init_events() {
			const thisClass = this;var template;
			document.body.addEventListener('insertInToGallery', (e) => {
				var images = thisClass.lastJson?.images??[];
				var imgsrc = thisClass.lastJson?.imgsrc??[];
				images.forEach((img, imgI) => {
					template = document.createElement('div');template.classList.add('acf-gallery-attachment', '-image', 'ui-sortable-handle');template.dataset.id = img.id;
					template.innerHTML = `
						<input type="hidden" name="acf[${thisClass.fields?.gallerymeta??''}][]" value="${img.id}">
						<div class="margin">
							<div class="thumbnail">
								<img src="${img.url}" alt="">
							</div>
						</div>
						<div class="actions">
							<a class="acf-icon -cancel dark acf-gallery-remove" href="#" data-id="${img.id}" title="Remove"></a>
						</div>
					`;
					document.querySelectorAll('.acf-gallery-main .acf-gallery-attachments').forEach((el) => {
						el.appendChild( template );
					});
				});
			});
		}
		init_flatpickr() {
			document.querySelectorAll('#aigpt3-acf-field-submitbox .score-text a').forEach((el) => {
				el.addEventListener('click', (event) => {
					event.preventDefault();
					document.querySelector('#aigpt3_scheduled_on').classList.toggle('should_open');
				});
			});
			document.querySelectorAll('#aigpt3_scheduled_on').forEach((el) => {
				flatpickr(el, {
					minDate: "today",
					enableTime: true,
					defaultDate: el.value
				});
			});
		}
		sendToServer( data ) {
			const thisClass = this;var message;
			$.ajax({
				url: thisClass.ajaxUrl,
				type: "POST",
				data: data,    
				cache: false,
				contentType: false,
				processData: false,
				success: function( json ) {
					thisClass.lastJson = json.data;
					var message = ( typeof json.data.message === 'string') ? json.data.message : (
						( typeof json.data === 'string') ? json.data : false
					);
					if( message ) {
						thisClass.toast.fire({icon: ( json.success ) ? 'success' : 'error', title: message})
					}
					if( json.data.hooks ) {
						json.data.hooks.forEach(( hook ) => {
							document.body.dispatchEvent( new Event( hook ) );
						});
					}
				},
				error: function( err ) {
					thisClass.toast.fire({icon: 'warning',title: err.responseText})
					console.log( err.responseText );
				}
			});
		}
		sendToOpenAI( data ) {
			const thisClass = this;var message, result;
			new Promise(( resolve, reject ) => {
				try {
					result = false;
					resolve( result );
				} catch (error) {
					if (error.response) {
					  console.log(error.response.status, error.response.data);
					  message = error.response.data;
					  thisClass.toast.fire({icon: 'error',title: message})
					} else {
					  message = error.message;
					  thisClass.toast.fire({icon: 'warning',title: message})
					}
					reject( message );
				}
			} ).then(( file ) => {
				console.log( file );
			});
		}

		// Popup form
		generate_formdata() {
			const thisClass = this;var formdata, json;
			if( thisClass.form && typeof thisClass.form !== 'undefined') {
				formdata = new FormData( thisClass.form );
				// json = JSON.stringify( Object.fromEntries( formdata ) );
				json = Object.fromEntries( formdata );
				json = thisClass.transformObjectKeys( json );
				var preview = document.querySelector('.profile-image-upload-preview');
				if( preview ) {json.cv.info.avater = preview.src;}
				if( thisClass.cv ) {
					if( thisClass.cv.tools ) {delete thisClass.cv.tools;}
					json.cv.tools = thisClass.cv;
				}
				return json;
			} else {
				return {};
			}
		}
		generate_submission() {
			const thisClass = this;
			if( thisClass.form && typeof thisClass.form !== 'undefined') {
				thisClass.form.addEventListener('submit', ( event ) => {
					event.preventDefault();
					var json = thisClass.generate_formdata();
					thisClass.generate_cv( json );
					json.cv.info.avater = '';json.i18n = {};json.svg = {};
					var formdata = new FormData();
					formdata.append('action', 'futurewordpress/project/advancedgpt3cvbuilder/cv/update');
					formdata.append('cv_id', thisClass.form.dataset.cv );
					formdata.append('_data', JSON.stringify( json ) );
					formdata.append('_nonce', thisClass.ajaxNonce );
					thisClass.sendToServer( formdata );
				});
			}
		}

		generate_fieldata() {
			const thisClass = this;thisClass.fields = {};
			thisClass.fields.title = document.querySelector('.editor-post-title__input') || document.querySelector('#titlewrap input[name=post_title]');
			thisClass.fields.gallerymeta = document.querySelector('.acf-gallery[id]')?.id??'';
			thisClass.fields.gallerymeta = thisClass.fields.gallerymeta.slice(4);
			thisClass.fields.galleryid = thisClass.config?.postid??false;
		}

		transformObjectKeys(obj) {
			const transformedObj = {};
			for (const key in obj) {
				if (obj.hasOwnProperty(key)) {
					const newKey = key.replace(/\[(\w+)\]/g, '.$1').replace(/^\./, '');
					const value = obj[key];
					if (typeof value === 'object') {
						transformedObj[newKey] = this.transformObjectKeys(value);
					} else {
						const keys = newKey.split('.');
						let currentObj = transformedObj;
						for (let i = 0; i < keys.length - 1; i++) {
							const currentKey = keys[i];
							if (!currentObj[currentKey]) {
								currentObj[currentKey] = {};
							}
							currentObj = currentObj[currentKey];
						}
						currentObj[keys[keys.length - 1]] = value;
					}
				}
			}
			return transformedObj;
		}
		handle_completion( data ) {
			const thisClass = this;var prompt, data, html, options = '';
			data = thisClass.lastJson?.data??[];
			if( data.choices && data.choices.length >= 1 ) {
				prompt = thisClass.lastJson.data.choices.map((choice) => choice.text).join('');
				// console.log( prompt );
				prompt = ( prompt.substring(0, 2) == "\n\n" ) ? prompt.substring(2) : prompt;
				thisClass.lastStatus = prompt;
				var fields = thisClass.get_editors();
				thisClass.editors.forEach((data) => {
					options += `<option value="${data.id}">${thisClass.strtocapitalize(data.name)}</option>`;
				});
				html = `
					<form action="#" name="acfgpt3_resform" id="acfgpt3_resform">
						<div class="form-row row p-0 m-0">
							<div class="col-md-12 single_block card">
								<div class="form-group">
									<div class="custom-control custom-checkbox mr-sm-2">
										<p class="m-0 p-0">
											<input type="checkbox" class="custom-control-input" id="popup_field-5" name="acfgpt3[title]">
											<label class="custom-control-label" for="popup_field-5">${thisClass.sprintf(thisClass.i18n?.iwdlk2replace??'I would like to replace "%s" with title field.', thisClass.lastReqs.prompt)}</label>
										</p>
									</div>
								</div>
							</div>
							<div class="col-md-12 single_block card">
								<div class="form-group ${( thisClass.editors.length >= 2 ) ? '' : 'd-none'}">
									<label class="mr-sm-2" for="popup_field-4">${thisClass.i18n?.select_field??'Select a field'}</label>
									<select id="popup_field-4" class="custom-select mr-sm-2" name="acfgpt3[editor]">
										<!-- <option value="" disabled selected>${thisClass.i18n?.select_editor??'Select Editor'}</option> -->
										${options}
									</select>
								</div>
								<p class="text">
									${thisClass.lastStatus.replaceAll( "\n", "<br/>" )}
								</p>
							</div>
						</div>
					</form>
				`;
				Swal.fire({
					title: thisClass.i18n?.slctanaction??'Select an action',
					confirmButtonText: thisClass.i18n?.letsdoit??'Let\'s do this',
					showCancelButton: false,
					icon: 'info',
					iconHtml: '<div class="dashicons dashicons-megaphone" style="transform: scale(3.5);"></div>',
					html: html,
					preConfirm: (login) => {
						data = thisClass.transformObjectKeys( Object.fromEntries( new FormData( document.querySelector('form[name=acfgpt3_resform]') ) ) )?.acfgpt3??{};
						thisClass.importin2Where = {editor: 'gutenburg', title: false, ...data};
						// try {} catch (error) {}
					},
					allowOutsideClick: false
				}).then((result) => {
					const editorType = thisClass.editors.find(item => item.id == thisClass.importin2Where?.editor??'');
					thisClass.check2importitle();
					if( ! editorType ) {
						thisClass.notify.fire( {
							icon: 'warn',
							title: thisClass.i18n?.somethingwentwrong??'Something went wrong!',
							text: `We can\'t identify "${editorType.name}" editor properly.`,
						});
						return;
					}
					switch ( editorType.type ) {
						case 'gutenburg':
							// if( typeof thisClass.importin2Gutenberg !== 'undefined') {
								new Promise((resolve, reject) => {
									var is_valid = thisClass.existorcreate_block();
									resolve( is_valid );
									if( false ) {reject('');}
								}).then((res) => {
									thisClass.importin2Gutenberg();
								}).catch((err) => {
									console.error(err);
								});
							// }
							break;
						case 'wysiwyg':
							// console.log('importin2Wysiwyg', thisClass.importin2Where?.editor??false );
							thisClass.importin2Wysiwyg();
							break;
						case 'title':
							if( thisClass.fields.title.nodeName == 'H1') {
								thisClass.fields.title.innerText = thisClass.lastJson.data.choices.map((txt) => txt.text);
							} else {
								var node = thisClass.fields.title.previousElementSibling;
								if( node && node.nodeName === 'LABEL') {node.classList.add('screen-reader-text');}
								thisClass.fields.title.value = thisClass.lastJson.data.choices.map((txt) => txt.text);
							}
							break;
						default:
							// console.log( editorType.type, 'Not found');
							thisClass.notify.fire( {
								icon: 'warn',
								title: thisClass.i18n?.somethingwentwrong??'Something went wrong!',
								text: `This type of "${editorType.type}" editor feature is not functioned here yet.`,
							});
							break;
					}
				});
			} else {
				thisClass.notify.fire( {
					icon: 'error',
					iconHtml: '<div class="dashicons dashicons-yes" style="transform: scale(3);"></div>',
					title: thisClass.i18n?.nothingreturned??'There is nothing to import!',
				});
			}
		}
		esc_attr(str) {
			// Replace & with &amp;
			str = str.replace(/&/g, '&amp;');
			// Replace ' with &#39;
			str = str.replace(/'/g, '&#39;');
			// Replace " with &quot;
			str = str.replace(/"/g, '&quot;');
			// Replace < with &lt;
			str = str.replace(/</g, '&lt;');
			// Replace > with &gt;
			str = str.replace(/>/g, '&gt;');
			return str;
		}
		esc_html(str) {
			return str;
			// return DOMPurify.sanitize(str, { 
			// 	ALLOWED_TAGS: [], 
			// 	ALLOWED_ATTR: []
			// });
		}
		esc_url(url) {
			var encodedUrl = encodeURI(url);
			return encodedUrl;
			// return DOMPurify.sanitize(encodedUrl);
		}
		sprintf(format, ...args) {
			let index = 0;
			return format.replace(/%[sd]/g, (match) => {
			  if (index >= args.length) {
				return match; // Return the original placeholder if there are no more arguments
			  }
			  if (match === '%s') {
				return String(args[index++]);
			  } else if (match === '%d') {
				return Number(args[index++]);
			  }
			  return match; // Return the original placeholder if it's not %s or %d
			});
		}
		strtocapitalize(str) {
			return str.charAt(0).toUpperCase() + str.slice(1);
		}
		async existorcreate_block() {
			const thisClass = this;
			if( typeof wp === 'undefined') {return false;}
			const selectedBlock = wp.data.select('core/block-editor').getSelectedBlock();
			const importedContent = thisClass?.lastStatus??'';	
			// console.log( [ importedContent, selectedBlock ] );
			const not_exists = ( ! selectedBlock || selectedBlock.name !== 'aigpt3/ai-generated-block');
			if( not_exists ) {
				const block = await wp.blocks.createBlock('aigpt3/ai-generated-block', { content: importedContent });
				wp.data.dispatch('core/block-editor').insertBlock(block);
			}
			return ( ! not_exists );
		}
		get_editors() {
			const thisClass = this;thisClass.editors = [];
			var title = thisClass.fields.title;
			if( title && false ) {
				thisClass.editors.push( {
					id: 'title',
					el: false,
					type: 'title',
					name: thisClass.i18n?.postitle??'Post title'
				});
			}
			var gutenburg = document.querySelectorAll('.edit-post-visual-editor, .edit-post-text-editor');
			if( gutenburg.length >= 1 ) {
				thisClass.editors.push( {
					id: 'gutenburg',
					el: false,
					type: 'gutenburg',
					name: thisClass.i18n?.gutenburgeditor??'Gutenburg Editor'
				});
			}
			if( typeof acf !== 'undefined' ) {
				acf.getFields().forEach((item) => {
					if( [ 'wysiwyg' ].includes( item.data.type) ) {
						thisClass.editors.push( {
							id: item.data.id,
							el: item.$el[0],
							type: item.data.type,
							name: item.data.name.replaceAll('_', ' ')
						});
						// tinymce.get( acf.getFields()[1].data.id ).setContent("<p>Hello worldsdfds!</p>");
						// tinymce.get('acf-editor-33').setContent("<p>Hello worldsdfds!</p>");
					}
				});
			}
			if( typeof tinymce !== 'undefined' ) {
				tinymce.editors.forEach(( editor ) => {
					var name = $( editor.id ).parents('.postbox').find('.postbox-header h2').text().replaceAll('_', ' '), id = editor.id;
					name = ( name && name !== '') ? name : id.replaceAll('_', ' ');
					// document.querySelectorAll( `#${id}:not([style*="display: none;"])` ).length >= 1
					if( ! [ 'acf_content' ].includes( id ) ) {
						thisClass.editors.push( {
							id: id,
							el: false,
							name: name,
							type: 'wysiwyg'
						});
					}
				});
			}
			return thisClass.editors;
		}
		importin2Wysiwyg() {
			const thisClass = this;
			var editor = thisClass.importin2Where?.editor??false;
			var importedContent = thisClass?.lastStatus??'';
			console.log( [editor, importedContent] );
			if( ! editor || tinymce === 'undefined') {return;}
			tinymce.get( editor ).setContent( importedContent.replaceAll( "\n", "<br/>" ) );
		}

		async generate_text( json ) {
			const thisClass = this;const data = {};
			
			data.model = json?.model??'text-davinci-003';
			data.prompt = json?.prompt??'';
			data.temperature = json?.temperature??0;
			data.max_tokens = json?.max_tokens??250;
			if( data.model !== '' && data.prompt !== '') {
				var result =  await thisClass.openai.createCompletion( data );
				thisClass.lastJson = result;
				return result;
			} else {
				throw new Error( thisClass.i18n?.mustprovidenesserydata??'You must provide necessery information before request.');
				return false;
			}
		}
		async generate_image( json ) {
			const thisClass = this;const data = {};
			const imagePromises = await thisClass.openai.createImage({
				prompt: json?.prompt??'',
				num_images: parseInt( thisClass.lastReqs?.num_images??1 ),
				size: json?.img_sizes??'512x512',
				// width: 200, // Thumbnail width
			});
			const imageUrls = imagePromises.data.data.map((img) => img.url );
			thisClass.lastImageUrls = imageUrls;
			return imageUrls;
		}
		async choose_image() {
			const thisClass = this;
			const returnedImages = await Swal.fire({
				title: thisClass.i18n?.selectimages??'Select Images',
				html: thisClass.generateImageHtml( thisClass.lastImageUrls ),
				showCancelButton: true,
				confirmButtonText: thisClass.i18n?.import_in2media??'Import into Media library',
				showLoaderOnConfirm: true,
				allowOutsideClick: false,
				showCancelButton: true,
				preConfirm: async () => {
				  var data = await thisClass.transformObjectKeys( Object.fromEntries( new FormData( document.querySelector('form[name=acfgpt3_popupimgs]') ) ) )?.acfgpt3??{};
				  thisClass.importin2Where = {title: false, ...data};
				  thisClass.selectedImages = Array.from( document.querySelectorAll('form#acfgpt3_popupimgs .gallery-fields input[type="checkbox"]:checked') ).map((checkbox) => checkbox.value ); // getAttribute('data-url')
					// thisClass.selectedImages = thisClass.importin2Where.images;
					if( thisClass.selectedImages.length <= 0 ) {
						Swal.showValidationMessage( thisClass.i18n?.imagenotselected??'Image not selected. Please select at least one to proceed.' );
						return false;
					} else {
						var formdata = new FormData();
						formdata.append('action', 'futurewordpress/project/aicontentgenerator/upload/medias');
						formdata.append('images', thisClass.selectedImages.join( "," ) );

						formdata.append('importImages', true );
						formdata.append('post_id', thisClass.fields.galleryid );

						formdata.append('toGallery', thisClass.importin2Where.toGallery );
						// document.querySelector('form#acfgpt3_popupimgs input[name="toGallery"]')?.value??false
						formdata.append('toPostType', thisClass.importin2Where.toPostType );
						// document.querySelector('form#acfgpt3_popupimgs input[name="toPostType"]')?.value??false

						formdata.append('_nonce', thisClass.ajaxNonce );
						thisClass.sendToServer( formdata );
						return thisClass.selectedImages;
					}
				}
			}).then((result) => {
				if( result.isConfirmed ) {
					thisClass.check2importitle();
				}
			})
			const selectedImages = returnedImages?.value??returnedImages;
			
			thisClass.lastJson = selectedImages;
			return selectedImages;
		}
		generateImageHtml( urls ) {
			const thisClass = this;
			var labels = urls.map((url) =>
				`<label>
					<img src="${url}" style="max-width:200px;max-height:200px;" />
					<input type="checkbox" data-url="" name="acfgpt3[images][]" value="${url}" />
				</label>`
			).join('');
			return `
				<form id="acfgpt3_popupimgs" name="acfgpt3_popupimgs" action="#">
					<div class="input-fields">
						<input type="text" name="acfgpt3[toGallery]" value="${thisClass.fields?.gallerymeta??''}" placeholder="${thisClass.i18n?.gallery_id??'Gallery ID'}">
						<input type="text" name="acfgpt3[toPostType]" value="${thisClass.config?.defgalpstyp??''}" placeholder="${thisClass.i18n?.gallerypostype??'Gallery post type'}">
					</div>
					<div class="form-group single_block card">
						<div class="custom-control custom-checkbox mr-sm-2">
							<p class="m-0 p-0">
								<input type="checkbox" class="custom-control-input" id="popup_field-5" name="acfgpt3[title]">
								<label class="custom-control-label" for="popup_field-5">${thisClass.sprintf(thisClass.i18n?.iwdlk2replace??'I would like to replace "%s" with title field.', thisClass.lastReqs.prompt)}</label>
							</p>
						</div>
					</div>
					<div class="gallery-fields">${labels}</div>
				</form>
			`;
		}
		check2importitle() {
			const thisClass = this;
			if( thisClass.importin2Where.title ) {
				if( thisClass.fields.title.nodeName == 'H1') {
					thisClass.fields.title.innerText = thisClass.lastReqs.prompt;
				} else {
					var node = thisClass.fields.title.previousElementSibling;
					if( node && node.nodeName === 'LABEL') {node.classList.add('screen-reader-text');}
					thisClass.fields.title.value = thisClass.lastReqs.prompt;
				}
			}
		}
	}

	new FWPListivoBackendJS();
} )( jQuery );